import React from 'react';
import { Card, Button, Row, Col, Container } from 'react-bootstrap';
import Layout from "../components/Layout";
import Hero from "../components/Hero";
import LinkedinButton from "../components/LinkedinButton";

const Portfolio = () => {
    return (
        <Layout>
            <Hero page={'PORTFOLIO'} description={'Mes realisations...'}></Hero>
        <section className="about section">
            <div className="section-inner-g">
                <div className="content">
                    <Row style={{ display: 'flex', flexWrap: 'wrap' }}>
                        {[
                            { count: '+ 10', category: 'Applications Web et Logicielles', link: '/development' },
                            { count: '+ 200', category: 'Conceptions Graphiques', link: '/design' },
                            { count: '+ 50', category: 'Photos De Tout Genre', link: '/photography' },
                            { count: '+ 15', category: 'Événements Auxquels J\'ai Participé', link: '/events' },
                        ].map((service, index) => (
                            <Col key={index} md={12} className="mx-0 mx-1 mb-3 d-flex justify-content-center align-items-center text-center width-full">
                                <Card style={{ textAlign: 'center' }}>
                                    <div className="col">
                                        <div className="overflow-hidden flex-nowrap width-full" style={{ textAlign: 'center' }}>
                                            <h1 className="mb-1 text-center py-2">
                                                <span className="text-reset">{service.count}</span>
                                            </h1>
                                            <p>{service.category}</p>
                                            <p style={{ textAlign: 'center' }}>
                                                <Button variant="cta-primary-1" href={service.link}>
                                                    Voir les détails
                                                </Button>
                                            </p>
                                        </div>
                                    </div>
                                </Card>
                            </Col>
                        ))}
                    </Row>

                    <LinkedinButton></LinkedinButton>
                </div>
            </div>
        </section>
        </Layout>
    );
};

export default Portfolio;
